<template>
  <div class="hello">
    <h1>{{ msg }}</h1>   

    <ul>      
      <br><li><router-link to="/page111">ตอนที่ 1</router-link></li><br> 
    </ul> 
    <article v-for="(location, idx) in locations" :key="idx">
      <img :src="location.image1">
    </article> 
    
    <ul>  
      <br><li><router-link to="/page131">ตอนที่ 2</router-link></li><br>    
    </ul>  
    <article v-for="(location, idx) in locations" :key="idx">
      <img :src="location.image4">
    </article>

    <ul>    
      <br><li><router-link to="/page323">ตอนที่ 3</router-link></li><br>  
    </ul> 
    <article v-for="(location, idx) in locations" :key="idx">
      <img :src="location.image7">
    </article>

    <ul>    
      <br><li><router-link to="/contact">Contact us</router-link></li><br>  
    </ul>
       
   
    <br><button v-on:click="logout">ออกจากระบบ</button>
           
  </div>
</template>

<script>
  import firebase from 'firebase'
import { db } from '../main'
import { compare } from 'semver';


export default {
  name: 'hello',
  data () {
    return {
      locations: []
    }
  },
  firestore () {
    return {
      locations: db.collection('locations')
    }
  },
  methods: {
    logout: function() {
      firebase.auth().signOut().then(() => {
        this.$router.replace('login')
       })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 px;
  font-size: 80px;
}
a {
  color: #b94842;
}
button {
  padding: 50px 50px;
  background: #b94842;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 15px;
  outline: 0;
  cursor: pointer;
}
</style>



